<template>
  <div>
    <button type="button" @click="test" class="btn btn-sm btn-primary">
      test
    </button>
    <button type="button" @click="test2" class="btn btn-sm btn-primary">
      test2
    </button>
  </div>
</template>
<script>
// import turf from "@turf/turf"
export default {
  data() {
    return {};
  },
  methods: {
      test2(){
        var center = [84.82512804700335, 26.241818082937552];
        var radius = 5;
        var options = {steps: 50, units: 'kilometers', properties: {foo: 'bar'}};
        var circle = turf.circle(center, radius, options);
        console.log(circle.geometry.coordinates) 
      },
    test(checkPoint, centerPoint, km) {
      function arePointsNear(checkPoint, centerPoint, km) {
        var ky = 40000 / 360;
        var kx = Math.cos((Math.PI * centerPoint.lat) / 180.0) * ky;
        var dx = Math.abs(centerPoint.lng - checkPoint.lng) * kx;
        var dy = Math.abs(centerPoint.lat - checkPoint.lat) * ky;
        return Math.sqrt(dx * dx + dy * dy) <= km;
      }
      var point = { lat: 59.615911, lng: 16.544232 };
      var radiuspoint = { lat: 59.345635, lng: 18.059707 };
      var n = arePointsNear(point, radiuspoint, 100);
      console.log(n);
    },
  },
};
